.training__container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    padding: 5rem 2.5rem 0 2.5rem;
}

.training__container-title {
    padding-bottom: .5rem;
    font-size: 3.5rem;
    font-weight: 400;
    line-height: 4rem;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .3s ease-in-out;
    background: transparent;
}

.training__container-paragraph {
    font-size: 1.5rem;
    font-weight: 300;
    line-height: 1.5rem;
    color: rgba(254, 255, 179, .7);
    padding-bottom: 2.5rem;
    letter-spacing: 4px;
    opacity: 0;
    transform: translateY(2rem);
    transition: all .3s ease-in-out;
    background: transparent;
}

.training__container-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 14rem;
    row-gap: 1.5rem;
}

.training__container-item {
    display: flex;
    width: 50rem;
    height: 26.5rem;
    border: 2px solid #FBE0F3;
    border-radius: 3px;
}

.training__container-item img {
    padding: 1rem 1.5rem 1rem 1rem;
}

.training__container-sub-title {
    font-size: 2.5rem;
    font-weight: 400;
    line-height: 2.5rem;
    color: #FEFFB3;
    padding: 2rem 0 1.5rem 0;
    text-align: start;
    margin-right: -1.5rem;
}

.training__container-sub-paragraph {
    font-size: 1rem;
    font-weight: 200;
    line-height: 1.25rem;
    letter-spacing: 1.5px;
    color: rgba(251, 224, 243, .7);
    text-align: start;
    padding-bottom: 1rem;
}

.training__container-buy-btn {
    border: 2px solid #FBE0F3;
    border-radius: 29px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    background: transparent;
    color: #FBE0F3;
    font-size: 1.5rem;
    font-weight: 400;
    line-height: 1.5rem;
    padding: 1rem 0 1rem 0;
    margin-top: auto;
    letter-spacing: 2.4px;
    transition: all .25s ease-in-out;
}

.training__container-buy-btn:hover, .training__container-buy-btn:active {
    background: #E18186;
    color: black;
    border: 2px solid #E18186;
}

.training__container-buy-btn svg {
    width: 1.25rem;
    height: 1.25rem;
    padding: 0;
    margin-left: 1.5rem;
}

.training__container-subitem {
    padding-bottom: 2rem;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding-right: 1.5rem;
}

.training__container-limited {
    font-size: 1.5rem;
    line-height: 1.5rem;
    font-weight: 300;
    letter-spacing: 4px;
    color: rgba(254, 255, 179, .7);
    padding: 2.5rem 0 5rem 0;
}

.visible {
    opacity: 1;
    transform: translateY(0);
}