.container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: #222023;
    max-width: 31.25rem;
    z-index: 10;
}

.wrapper {
    left: 0;
    top: 0;
    position: absolute;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, .5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.paragraph {
    font-size: 2rem;
    line-height: 2rem;
    font-weight: 500;
    color: #FEFFB3;
    padding: 3rem 0 4rem 0;
}

.input {
    background: transparent;
    border-bottom: 2px solid #E18186;
    min-height: 2.25rem;
    height: 100%;
}

.input:first-child {
    margin-bottom: 4rem;
}

.input:active, input:focus {
    border-bottom: 2px solid #E18186;
    outline: none;
}

.input::placeholder {
    color: white;
    font-size: 1.75rem;
    line-height: 1.5rem;
    font-weight: 600;
    padding: .25rem 0 .25rem 0;
    text-align: center;
}